*, :before, :after {
  box-sizing: border-box;
  color: inherit;
  border: 0;
  margin: 0;
  padding: 0;
}

ul, ol, li {
  list-style: none;
}

a, a:visited, a:hover {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

img {
  vertical-align: top;
}

html {
  height: 100%;
}

body {
  color: #000;
  background-color: #fff;
