/* ОБНУЛЕНИЕ СТИЛЕЙ */

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    color: inherit;
}

ul,
ol,
li {
    list-style: none;
}


a,
a:visited {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

img {
    vertical-align: top;
}

html {
    height: 100%;
}

body {
    background-color: $page-background;
    font-family: $arial, Helvetica, sans-serif;
    font-weight: $regular;
    font-size: toRem(18);
    color: $main-text;
    line-height: 1;
    height: 100%;
}
